<template>
    <div class="message-head">
        <h3 class="text-center">公共报文头</h3>
        <h4 class="page-header" id="common-head">参数说明</h4>
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th>中文名称</th>
                    <th>名称</th>
                    <th>是否必填</th>
                    <th>类型</th>
                    <th>备注</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in commonHeader">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.required}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                commonHeader: [
                    {chnName: '流水号', name: 'ESB-TRANNO', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '来源系统标识', name: 'ESB-ORISYS', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '路由', name: 'ESB-ROUTE', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '签名', name: 'ESB-SIGNATURE', required: 'N', type: 'String', remarks: ''},
                    {chnName: '机构号', name: 'ESB-BRANCHNO', required: 'N', type: 'String', remarks: ''}
                ]
            }
        }
    }
</script>
<style lang="stylus">
    .message-head
        height: 600px
</style>


