<template>
    <div class="scroll-top" @click="scrollTopFn" id="back-to-top" v-on:mouseenter="showIcon = false" v-on:mouseleave="showIcon = true">
        <span v-if="showIcon" class="glyphicon glyphicon-triangle-top"></span>
        <span v-if="!showIcon" class="txt">TOP</span>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                showIcon: true
            }
        },
        mounted () {
            $(window).scroll(function() {
                if ($(window).scrollTop() > 100) {
                    $("#back-to-top").fadeIn(100);
                } else {
                    $("#back-to-top").fadeOut(100);
                }
            });
        },
        methods: {
            scrollTopFn() {
                $('body,html').animate({scrollTop: 0}, 100);
            }
        }
    }
</script>
<style lang="stylus">
    .scroll-top
        position: fixed
        right: 20px
        bottom: 30px
        width: 36px
        height: 36px
        line-height: 40px
        border-radius: 50%
        text-align: center
        cursor: pointer
        background-color: #ddd
        overflow: hidden
        span
            display: block
            position: relative
        .glyphicon
            color: #888
            top: 8px
        .txt
            font-size: 12px
            top: -2px
</style>


