<script>
import InParamTable from '../common/InParamTable';
import OutParamTable from '../common/OutParamTable';
export default {
    components: {
        'v-inParamTable': InParamTable,
        'v-outParamTable': OutParamTable
    },
    data () {
        return {
            loadCacheResParams: [
                {chnName: '返回码', name: 'code', type: 'String', remarks: '0000-加载成功；其他均表示失败'}
            ],
            eqViewSummaryInfoParams: [
                {chnName: '证件类别', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件号码', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '姓名', name: 'name', required: 'N', type: 'String', remarks: ''},
                {chnName: '性别', name: 'sex', required: 'N', type: 'String', remarks: ''},
                {chnName: '生日', name: 'birthDate', required: 'N', type: 'String', remarks: 'yyyy-MM-dd格式'}
            ],
            eqViewSummaryInfoResParams: [
                {chnName: '姓名', name: 'custName', type: 'String', remarks: ''},
                {chnName: '证件类型', name: 'custIdType', type: 'String', remarks: ''},
                {chnName: '证件号码', name: 'custIdNo', type: 'String', remarks: ''},
                {chnName: '性别', name: 'custSex', type: 'String', remarks: ''},
                {chnName: '生日', name: 'custBirthDate', type: 'String', remarks: 'yyyy-MM-dd格式'},
                {chnName: '证件有效期', name: 'idInvalidDate', required: 'N', type: 'varchar(10)', remarks: 'yyyy-MM-dd格式'},
                {chnName: '保单总数', name: 'policyQuantity', type: 'String', remarks: ''},
                {chnName: '有效保单数', name: 'effectiveQuantity', type: 'String', remarks: ''},
                {chnName: '失效保单数', name: 'invalidQuantity', type: 'String', remarks: ''},
                {chnName: '终止保单数', name: 'terminateQuantity', type: 'String', remarks: ''},
                {chnName: '垫缴保单数', name: 'mafpQuantity', type: 'String', remarks: ''},
                {chnName: '宽限期保单数', name: 'grancePeriodQuantity', type: 'String', remarks: ''},
                {chnName: '待交保费', name: 'totalPremium', type: 'String', remarks: ''},
                {chnName: '待领取生存金', name: 'totalEndowAmnt', type: 'String', remarks: ''},
                {chnName: '已领取生存金', name: 'paidEndowAmnt', type: 'String', remarks: ''},
                {chnName: '待还借款', name: 'totalrepayment', type: 'String', remarks: ''},
