<template>
    <div>
        <h3 class="text-center">接入说明</h3>
        <h4 class="page-header" id="common-head">接入方式</h4>
        <p>保全平台接入方式分为页面级别和服务级别的接入两种。</p>
        <h5><b>1. 页面级别接入：</b></h5>
        <p>页面级别接入需要先联系保全平台注册使用权限，获取接入秘钥accessKey和md5加密salt, 并根据本文档规范进行接入，未经授权的渠道无法访问保全云服务相关功能。</p>
        <h5><b>2. 服务级别接入：</b></h5>
        <p>服务级别接入即调用保全相关业务的服务接口</p>
    </div>
</template>
