        <h4 class="page-header">索引</h4>
        <ul>
          <li><a href="" @click.prevent="custormAnchor('flowchart')">流程说明</a></li>
          <li><a href="" @click.prevent="custormAnchor('survival01')">一、生存金领取初筛查询</a></li>
          <li><a href="" @click.prevent="custormAnchor('survival02')">二、生存金领取受理规则检查接口</a></li>
          <li><a href="" @click.prevent="custormAnchor('survival03')">三、生存金领取银行账号查询接口</a></li>
          <li><a href="" @click.prevent="custormAnchor('survival04')">四、生存金领取受理提交</a></li>
          <li><a href="" @click.prevent="custormAnchor('survival05')">五、生存金领取受理信息查询</a></li>
        </ul>
        <h4 class="page-header" id="flowchart">流程说明</h4>
        <img src="./survival.png" alt="">
        <h4 class="page-header" id="survival01">一、生存金领取初筛查询</h4>
        <h5 class="mt20">1.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/availableSurvivalPolService/query</p>
        <h5 class="mt20">1.2 入参说明</h5>
        <v-inParamTable :tableData="survialQueryParams"></v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{"idType": "I", idNo: "110104195801220927", "name": "王妫倦", "sex": "F", "birthDate": "1958-01-22"}</pre>
        <h5 class="mt20">1.3 出参说明</h5>
        <v-outParamTable :hasSlot="true" isList=true>
            <tr v-for="item in survialQueryResParams01">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-info text-center">开始 + 生存金领取详情保单列表开始csMtnSurvivalInfoBOList&ltlist&gt</td>
            </tr>
            <tr v-for="item in survialQueryResParams02">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-info text-center">结束 + 生存金领取详情保单列表开始csMtnSurvivalInfoBOList&ltlist&gt</td>
            </tr>
        </v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>[{"cntrNo":"2011110244454015017674","cntrStat":"K","cntrSurvivalAmnt":7567.86,"cntrSurvivalIntAmnt":1133.4899999999998,"csMtnSurvivalInfoBOList":[{"accOpenDate":1466179200000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":66.98,"survivalIntTermDate":1513661625000,"survivalType":"EO"},{"accOpenDate":1434556800000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":113.6,"survivalIntTermDate":1513661625000,"survivalType":"EO"},{"accOpenDate":1403020800000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":161.72,"survivalIntTermDate":1513661625000,"survivalType":"EO"},{"accOpenDate":1371484800000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":211.53,"survivalIntTermDate":1513661625000,"survivalType":"EO"},{"accOpenDate":1339948800000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":263.08,"survivalIntTermDate":1513661625000,"survivalType":"EO"},{"accOpenDate":1308326400000,"cntrNo":"2011110244454015017674","survivalAmnt":1261.31,"survivalIntAmnt":316.58,"survivalIntTermDate":1513661625000,"survivalType":"EO"}],"hldrCustNo":"2010110244500122216","hldrIdNo":"110104195801220927","hldrIdType":"I","hldrName":"王妫倦","ipsnCustNo":"2010110244500122216","ipsnIdNo":"110104195801220927","ipsnIdType":"I","ipsnName":"王妫倦","mgrBranchNo":"110244","polCode":"454","polName":"国寿福满一生两全保险（分红型）","salesBranchNo":"110244","salesChannel":"BS","salesNo":"PH0583","sysNoOriginal":"1"}]</pre>
        <h4 class="page-header" id="survival02">二、生存金领取受理规则检查接口</h4>
        <h5 class="mt20">2.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/checkAcceptRuleService/ruleCheck</p>
        <h5 class="mt20">2.2 入参说明</h5>
        <v-commonSubmit>
            <tr>
                <td colspan="5" class="bg-success text-center">开始 + 业务受理信息开始businessBOList&ltlist&gt</td>
            </tr>
            <tr v-for="item in ruleCheckParams">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.required}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-success text-center">结束 + 业务受理信息开始businessBOList&ltlist&gt</td>
            </tr>
        </v-commonSubmit>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>
        {
            "businessBOList":[{ "accOpenDate": 1453046400000, "cntrNo": "2006112102S91015000105", "survivalAmnt": 2653.3, "survivalIntAmnt": 179.09, "survivalIntTermDate": 1512960527000, "survivalType": "EO"}],
