            "csMtnInfoBO":{ "acceptChannel": "V", "applBirthDate": "1956-11-11", "applIdNo": "11010219561111344X", "applIdType": "I", "applName": "王路樱", "applSex": "F", "custLevel": "", "custRiskLevel": "", "idValidDate": "9999-12-31 00:00:00", "isAssignee": "0", "mtnApplyNo": "20170000000000011286"},
            "csMtnInfoDtlBOList":[{ "amnt": 5306.6, "cntrNo": "2006112102S91015000105", "hldrIdNo": "11010219561111344X", "hldrIdType": "I", "hldrName": "王路樱", "ipsnIdNo": "11010219561111344X", "ipsnIdType": "I", "ipsnName": "王路樱", "mgrBranchNo": "112102", "mtnApplyNo": "20170000000000011286", "mtnNo": "", "polCode": "S91", "salesBranchNo": "112102", "salesNo": "16436", "sysNoOriginal": "1"}],
            "csMtnInfoLstBO":{ "mtnApplyNo": "20170000000000011286", "mtnItemCode": "SA", "mtnNo": "", "totalAmnt": 6196.6900000000005},
            "csMtnRpInfoBO":{ "accCustIdNo": "11010219561111344X", "accCustIdType": "I", "accCustName": "王路樱", "bankAccInType": "1", "bankAccNo": "", "bankCode": "", "grantFlag": "0", "mioType": "T", "mtnNo": ""}
        }</pre>
        <h5 class="mt20">2.3 出参说明</h5>
        <v-outParamTable :hasSlot="true">
            <tr>
                <td>受理结果信息</td>
                <td>msg</td>
                <td>String</td>
                <td></td>
            </tr>
        </v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>{"msg": "success"}</pre>

        <h4 class="page-header" id="survival03">三、生存金领取银行账号查询接口</h4>
        <h5 class="mt20">2.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/bankQueryService/bankQuery</p>
        <h5 class="mt20">2.2 入参说明</h5>
        <v-inParamTable :tableData="bankQueryParams"></v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{"idNo": "110222195010105429", "idType": "I", "name": "张秀"}</pre>
        <h5 class="mt20">2.3 出参说明</h5>
        <v-outParamTable :tableData="bankQueryResParams" :isList="true"></v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>[{"accCustName":"张秀","bankAccNo":"0200269001022677638","bankCode":"1101","bankName":"中国工商银行","bankaccIdNo":"110222195010105429","bankaccIdType":"I"}]</pre>

        <h4 class="page-header" id="survival04">四、生存金领取受理提交</h4>
        <h5 class="mt20">4.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/mtnSurvivalCommitService/accept</p>
        <h5 class="mt20">4.2 入参说明</h5>
        <v-commonSubmit>
            <tr>
                <td colspan="5" class="bg-success text-center">开始 + 业务受理信息businessBOList&ltlist&gt</td>
