            <tr v-for="item in ruleCheckParams">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.required}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-success text-center">结束 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr>
        </v-commonSubmit>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>
        {
            "businessBOList": [{ "accOpenDate": 1453046400000, "cntrNo": "2006112102S91015000105", "survivalAmnt": 2653.3, "survivalIntAmnt": 179.09, "survivalIntTermDate": 1512960527000, "survivalType": "EO"}],
            "csMtnAccClerkInfoBO": { "acceptBranchNo": "000000", "acceptClerkName": "", "acceptClerkNo": "csbps", "mtnApplyNo": "20170000000000011291"},
            "csMtnAssigneeInfoBO": {""mtnApplyNo"": "20170000000000011291"},
            "csMtnIdentityBO": {"identityResult": "1", "identityType": "4", "mtnApplyNo": "20170000000000011291"},
            "csMtnInfoBO": { "acceptChannel": "V", "applBirthDate": "1956-11-11", "applIdNo": "11010219561111344X", "applIdType": "I", "applName": "王路樱", "applSex": "F", "custLevel": "", "custRiskLevel": "", "idValidDate": "9999-12-31 00:00:00", "isAssignee": "0", "mtnApplyNo": "20170000000000011291"},
            "csMtnInfoDtlBOList": [{ "amnt": 6196.6900000000005, "calAmnt": 5306.6, "cntrNo": "2006112102S91015000105", "hldrIdNo": "11010219561111344X", "hldrIdType": "I", "hldrName": "王路樱", "ipsnIdNo": "11010219561111344X", "ipsnIdType": "I", "ipsnName": "王路樱", "mgrBranchNo": "112102", "mtnApplyNo": "20170000000000011291", "mtnNo": "", "polCode": "S91", "salesBranchNo": "112102", "salesChannel": "PA", "salesNo": "16436", "sysNoOriginal": "1"}],
            "csMtnInfoLstBO": { "mtnApplyNo": "20170000000000011291", "mtnItemCode": "SA", "acceptBranchNo": "110000", "acceptClerkName": "csbps", "acceptClerkNo": "csbps", "totalAmnt": 6196.6900000000005},
            "csMtnRpInfoBO": { "accCustIdNo": "11010219561111344X", "accCustIdType": "I", "accCustName": "王路樱", "bankAccInType": "1", "bankAccNo": "0200210501001368068", "bankCode": "1101", "grantFlag": "1", "mioType": "T"}
        }</pre>
        <h5 class="mt20">4.3 出参说明</h5>
        <v-outParamTable :hasSlot="true">
            <tr>
                <td>保全业务顺序号</td>
                <td>mtnSeqNo</td>
                <td>String</td>
                <td>用于电子凭证查询</td>
            </tr>
        </v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>{"mtnSeqNo": "200010001"}</pre>

        <h4 class="page-header" id="survival05">五、生存金领取受理信息查询</h4>
        <h5 class="mt20">5.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/mtnSurvivalAcceptQueryService/query</p>
        <h5 class="mt20">5.2 入参说明</h5>
        <v-inParamTable :hasSlot=true>
            <tr>
                <td>业务凭证号</td>
                <td>mtnNo</td>
                <td>Y</td>
                <td>String</td>
                <td></td>
            </tr>
        </v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{"mtnNo":"2017110000PSA000003431"}</pre>
        <h5 class="mt20">5.3 出参说明</h5>
        <v-commonOutParams>
            <tr>
                <td colspan="5" class="bg-info text-center">开始 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr>
            <tr v-for="item in mtnSurvivalAcceptQueryParams">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-info text-center">结束 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr>            
        </v-commonOutParams>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>
        {
            "businessBOList": [{ "accOpenDate": 1453046400000, "cntrNo": "2006112102S91015000105", "id": "201", "mtnNo": "2017110000PSA000003431", "survivalAmnt": 2653.3, "survivalIntAmnt": 179.09, "survivalIntTermDate": 1512975336000, "survivalType": "EO"}],
            "csMtnAccClerkInfoBO": { "acceptBranchNo": "000000", "acceptClerkNo": "csbps", "id": 0, "mtnApplyNo": "20170000000000011291"},
            "csMtnAssigneeInfoBO": {"id": 0},
            "csMtnIdentityBO": { "id": 0, "identityResult": "1", "identityType": "4", "mtnApplyNo": "20170000000000011291"},
            "csMtnInfoBO": { "acceptChannel": "V", "acceptTime": "2017-12-11", "applBirthDate": -414662400000, "applIdNo": "11010219561111344X", "applIdType": "I", "applName": "王妫倦", "applSex": "F", "id": 0, "idValidDate": 253402185600000, "isAssignee": "0"},
            "csMtnInfoDtlBOList": [{ "amnt": 6196.69, "calAmnt": 5306.6, "cntrNo": "2006112102S91015000105", "hldrIdNo": "11010219561111344X", "hldrIdType": "I", "hldrName": "王妫倦", "id": 4051, "ipsnIdNo": "11010219561111344X", "ipsnIdType": "I", "ipsnName": "王妫倦", "mgrBranchNo": "112102", "mtnApplyNo": "20170000000000011291", "mtnNo": "2017110000PSA000003431", "polCode": "S91", "salesBranchNo": "112102", "salesChannel": "PA", "salesNo": "16436", "sysNoOriginal": "1"}],
            "csMtnInfoLstBO": { "acceptBranchNo": "110000", "acceptClerkName": "csbps", "acceptClerkNo": "csbps", "acceptTime": 1512975336000, "id": 2911, "mtnApplyNo": "20170000000000011291", "mtnItemCode": "SA", "mtnNo": "2017110000PSA000003431", "mtnProcStat": "12", "mtnSeqNo": "2017SA0000002811", "totalAmnt": 6196.69, "totalCalAmnt": 5306.6, "vclkBranchNo": "000000", "vclkNo": "sys", "verifyTime": 1512975343000},
            "csMtnRpInfoBO": { "accCustIdNo": "11010219561111344X", "accCustIdType": "I", "accCustName": "王妫倦", "bankAccInType": "1", "bankAccNo": "0200210501001368068", "bankCode": "1101", "id": 2281, "mioType": "T", "mtnNo": "2017110000PSA000003431"}
        }</pre>

    </div>
</template>
<script>
import InParamTable from '../common/InParamTable';
import OutParamTable from '../common/OutParamTable';
import CommonSubmit from '../common/CommonSubmit';
import CommonOutParams from '../common/CommonOutParams';
export default {
    components: {
        'v-inParamTable': InParamTable,
        'v-outParamTable': OutParamTable,
        'v-commonSubmit': CommonSubmit,
        'v-commonOutParams': CommonOutParams
    },
    data () {
        return {
            survialQueryParams: [
                {chnName: '客户姓名', name: 'name', required: 'N', type: 'String', remarks: ''},
                {chnName: '证件类型', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件号码', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '性别', name: 'sex', required: 'N', type: 'String', remarks: ''},
                {chnName: '出生日期', name: 'birthDate', required: 'N', type: 'String', remarks: ''}
            ],
            survialQueryResParams01: [
                {chnName: '保单号', name: 'cntrNo', type: 'String', remarks: ''},
                {chnName: '保单状态', name: 'cntrStat', type: 'String', remarks: ''},
                {chnName: '所属系统', name: 'sysNoOriginal', type: 'String', remarks: ''},
                {chnName: '管理机构号', name: 'mgrBranchNo', type: 'String', remarks: ''},
                {chnName: '险种名称', name: 'polName', type: 'String', remarks: ''},
                {chnName: '险种代码', name: 'polCode', type: 'String', remarks: ''},
                {chnName: '原因', name: 'reason', type: 'String', remarks: ''},
                {chnName: '投保人客户号', name: 'hldrCustNo', type: 'String', remarks: ''},
                {chnName: '投保人姓名', name: 'hldrName', type: 'String', remarks: ''},
                {chnName: '投保人证件号', name: 'hldrIdNo', type: 'String', remarks: ''},
                {chnName: '投保人证件类型', name: 'hldrIdType', type: 'String', remarks: ''},
                {chnName: '被保人客户号', name: 'ipsnCustNo', type: 'String', remarks: ''},
                {chnName: '被保人姓名', name: 'ipsnName', type: 'String', remarks: ''},
                {chnName: '被保人证件类型', name: 'ipsnIdType', type: 'String', remarks: ''},
                {chnName: '被保人证件号', name: 'ipsnIdNo', type: 'String', remarks: ''},
                {chnName: '销售员机构', name: 'salesBranchNo', type: 'String', remarks: ''},
                {chnName: '销售员工号', name: 'salesNo', type: 'String', remarks: ''},
                {chnName: '销售渠道', name: 'salesChannel', type: 'String', remarks: ''},
                {chnName: '待领生存金', name: 'cntrSurvivalAmnt', type: 'double', remarks: ''},
                {chnName: '待领生存金利息', name: 'cntrSurvivalIntAmnt', type: 'double', remarks: ''}
            ],
            survialQueryResParams02: [
                {chnName: '作业凭证号', name: 'mtnNo', type: 'String', remarks: ''},
                {chnName: '保单号', name: 'cntrNo', type: 'String', remarks: ''},
                {chnName: '生存金类型', name: 'survivalType', type: 'String', remarks: ''},
                {chnName: '开户日期', name: 'accOpenDate', type: 'Date', remarks: ''},
                {chnName: '利息计算截止日期', name: 'survivalIntTermDate', type: 'Date', remarks: ''},
                {chnName: '金额', name: 'survivalAmnt', type: 'Double', remarks: ''},
                {chnName: '利息', name: 'survivalIntAmnt', type: 'Double', remarks: ''}
            ],
            bankQueryParams: [
                {chnName: '姓名', name: 'name', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件类型', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件号码', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '性别', name: 'sex', required: 'N', type: 'String', remarks: ''},
                {chnName: '出生日期', name: 'birthDate', required: 'N', type: 'String', remarks: ''}
            ],
            bankQueryResParams: [
                {chnName: '帐户所有人名称', name: 'accCustName', type: 'String', remarks: ''},
                {chnName: '银行代码', name: 'bankCode', type: 'String', remarks: ''},
                {chnName: '银行名称', name: 'bankName', type: 'String', remarks: ''},
                {chnName: '银行帐号', name: 'bankAccNo', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证件类型', name: 'bankaccIdType', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证件类型中文', name: 'bankaccIdTypeChn', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证号', name: 'bankaccIdNo', type: 'String', remarks: ''},
                {chnName: '预留手机号', name: 'mobile', type: 'String', remarks: ''}
            ],
            ruleCheckParams: [
                {chnName: '开户日期', name: 'accOpenDate', required: 'Y', type: 'Date', remarks: ''},
                {chnName: '保单号', name: 'cntrNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '金额', name: 'survivalAmnt', required: 'Y', type: 'Double', remarks: ''},
                {chnName: '利息', name: 'survivalIntAmnt', required: 'Y', type: 'Double', remarks: ''},
                {chnName: '利息计算截止日期', name: 'survivalIntTermDate', required: 'Y', type: 'Date', remarks: ''},
                {chnName: '生存金类型', name: 'survivalType', required: 'Y', type: 'String', remarks: ''},
            ],
            mtnSurvivalAcceptQueryParams: [
                {chnName: '开户日期', name: 'accOpenDate', type: 'Date', remarks: ''},
                {chnName: '保单号', name: 'cntrNo', type: 'String', remarks: ''},
                {chnName: '业务凭证号', name: 'mtnNo', type: 'String', remarks: ''},
                {chnName: '金额', name: 'survivalAmnt', type: 'Double', remarks: ''},
                {chnName: '利息', name: 'survivalIntAmnt', type: 'String', remarks: ''},
                {chnName: '利息计算截止日期', name: 'survivalIntTermDate', type: 'Date', remarks: ''},
                {chnName: '生存金类型', name: 'survivalType', type: 'String', remarks: ''}

            ]

        }
    },
    mounted() {
        $('body,html').animate({scrollTop: 0}, 0);
    },
    methods: {
        custormAnchor(anchorName) {
            // 找到锚点
            let anchorElement = document.getElementById(anchorName);
            // 如果对应id的锚点存在，就跳转到锚点
            if(anchorElement) { anchorElement.scrollIntoView(); }
        }
    }
}
</script>
<style>
    
</style>


