<template>
    <div>        
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th width="120">中文名称</th>
                    <th width="120">名称</th>
                    <th width="120">是否必填</th>
                    <th width="80">类型</th>
                    <th>备注</th>
                </tr>
            </thead>
            <tbody v-if="!hasSlot && !isNull">
                <tr>
                    <td colspan="5" class="bg-danger text-center">公共报文头 + </td>
                </tr>
                <tr><td colspan="5"></td></tr>

                <tr>
                    <td colspan="5" class="bg-danger text-center">开始 + 交易报文  <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <tr v-if="isPaging">
                    <td>当前分页页数</td><td>page</td><td>Y</td><td>Number</td><td></td>
                </tr>
                <tr v-if="isPaging">
                    <td>每页数据条数</td><td>rows</td><td>N</td><td>Number</td><td>默认10</td>
                </tr>
                <tr v-if="isPaging"><td colspan="5"></td></tr>
                <tr v-for="item in tableData">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.required}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
                <tr>
                    <td colspan="5" class="bg-danger text-center">结束 + 交易报文 <span v-if="isList">&ltlist&gt</span> </td>
                </tr>
            </tbody>
            <tbody v-if="hasSlot && !isNull">
                <tr>
                    <td colspan="5" class="bg-danger text-center">公共报文头 + </td>
                </tr>
                <tr><td colspan="5"></td></tr>
                <tr>
                    <td colspan="5" class="bg-danger text-center">开始 + 交易报文<span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <slot></slot>
                <tr>
                    <td colspan="5" class="bg-danger text-center">结束 + 交易报文<span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
            <tbody v-if="isNull">
                <tr><td colspan="5" class="text-center">无</td></tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        props: {
            isNull: {
                type: Boolean,
                default: false
            },
            isList: {
                type: Boolean,
                default: false
            },
            isPaging: {
                type: Boolean,
                default: false
            },
            hasSlot: {
                type: Boolean,
                default: false
            },
            tableData: {
                type: Array,
                default () {
                    return [
                        {chnName: '证件类别', name: 'custIdType', required: 'Y', type: 'String', remarks: ''},
                        {chnName: '证件号码', name: 'custIdNo', required: 'Y', type: 'String', remarks: ''}
                    ]
                }
            }
        },
        data() {
            return  {

            }
        }
    }
</script>
<style>
    
</style>


