<template>
    <div>        
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th>中文名称</th>
                    <th>名称</th>
                    <th>类型</th>
                    <th>备注</th>
                </tr>
            </thead>
            <tbody v-if="!hasSlot">
                <tr>
                   <td colspan="5" class="bg-danger text-center">开始 + 查询结果 <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <tr v-if="tableData.length > 0" v-for="item in tableData">
                    <td>{{item.chnName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.type}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
                <tr>
                   <td colspan="5" class="bg-danger text-center">结束 + 查询结果<span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
            <tbody v-if="hasSlot">
                <tr v-if="!isNull">
                   <td colspan="5" class="bg-danger text-center">开始 + 查询结果 <span v-if="isList">&ltlist&gt</span></td>
                </tr>
                <slot></slot>
                <tr v-if="!isNull">
                   <td colspan="5" class="bg-danger text-center">结束 + 查询结果<span v-if="isList">&ltlist&gt</span></td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    export default {
        props: {
            hasSlot: {  // 是否是自定义内容
                type: Boolean,
                default: false
            },
            isList: {  // 是否是数组
                type: Boolean,
                default: false
            },
            isNull: {  // 无数据标记
                type: Boolean,
                default: false
            },
            tableData: { // 传参
                type: Array,
                default () {
                    return []
                }
            }
        },
        data() {
            return {}
        }
    }
</script>
<style></style>


