<template>
    <div class="doc-wrapper">
        <h3 class="text-center">保全云服务接入文档</h3>
        <h4 class="page-header">索引</h4>
        <ul>
          <li><a href="" @click.prevent="custormAnchor('flowchart')">流程说明</a></li>
          <li><a href="" @click.prevent="custormAnchor('desc')">功能描述</a></li>
          <li><a href="" @click.prevent="custormAnchor('useScene')">使用场景</a></li>
          <li><a href="" @click.prevent="custormAnchor('accessAddr')">接入地址</a></li>
          <li><a href="" @click.prevent="custormAnchor('paramIntro')">参数说明</a></li>
        </ul>   
        <h4 class="page-header" id="flowchart">流程说明</h4>
        <img src="./page-level-access.png" alt="">
        <h4 class="page-header" id="desc">一、功能描述</h4>
        <p>接入保全平台云服务相关功能，需要先联系保全平台注册使用权限，获取接入秘钥accessKey和md5加密salt, 并根据本文档规范进行接入，未经授权的渠道无法访问保全云服务相关功能。</p>
        <h4 class="page-header" id="useScene">二、使用场景</h4>
        <h5>2.1 客户未经过身份认证需要在保全平台进行认证</h5>
        <p>此场景通过权限认证后，会跳转至保全平台身份认证页面，客户认证通过后，会进入相应云服务功能的页面。</p>
        <h5 class="mt20">2.2 客户已经过身份认证</h5>
        <p>客户已通过接入方系统的身份验证（如：客户已通过e宝账账户密码验证），接入参数中需带有认证结果和客户信息，访问接入地址后通过权限认证将直接进入相应云服务功能页面。</p>
        <p>适应于：e宝账 </p>
        <h4 class="page-header" id="accessAddr">三、接入地址</h4>
        <h5>3.1 测试环境：</h5>
        <p class="bg-success">http://www.biglee.pw/csbps/access/web/access?channel_id=&pa_enc_data=AES(param,accessKey)&pa_enc_hash=md5(encData+salt)</p>
        <h5 class="mt20">3.2 说明：</h5>
        <table class="table table-bordered">
            <thead>
                <tr>
                    <th>channel_id</th>
                    <th>pa_enc_data</th>
                    <th>pa_enc_hash</th>
                </tr>                
            </thead>
            <tbody>
                <tr>
                    <td>渠道代码（如e店为”ESS”）</td>
                    <td>对动态参数AES加密后得到的HEX格式字符串即AES(param,accessKey)</td>
                    <td>对加密后数据pa_enc_data+salt之后md5 hash后得到的hash值</td>
                </tr>                
            </tbody>
        </table>
        <p class="bg-danger">AES各开发语言加解密范例可见附录3，加密秘钥accessKey及md5 salt由保全平台提供</p>
        <h4 class="page-header" id="paramIntro">四、参数说明</h4>
        <table class="table table-bordered table-striped table-hover">
            <thead>
                <tr>
                    <th>参数描述</th>
                    <th>参数名称</th>
                    <th>是否必填</th>
                    <th></th>
                    <th>备注</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in params">
                    <td>{{item.desc}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.required}}</td>
                    <td>{{item.defaults}}</td>
                    <td>{{item.remarks}}</td>
                </tr>
            </tbody>
        </table>
        <h5 class="mt20">动态参数param示例：</h5>
        <pre>
        {
        "channel_id": "ESS",  
        "app_id": "101",
        "identity_result": "0",
        "id_no": "110101199901011111",
        "id_type": "I",
        "name": "lisi",
        "sex": "F",
        "birth_date": "1999-01-01",
        "clerk_no": "11001234",
        "clerk_id_type": "I",
        "clerk_id_no": "110101199901011111",
        "clerk_name": "zhangsan",
        "clerk_type": "0",
        "branch_no": "110000"
        }
        </pre>
    </div>
</template>
<script>
export default {
    data () {
        return {
            params: [
                {desc: '渠道id', name: 'channel_id', required: 'Y', defaults: '', remarks:'如e店为”ESS”，详见附录1'},
                {desc: '应用id', name: 'app_id', required: 'Y', defaults: '', remarks:'如客户权益为”101”，详见附录2'},
                {desc: '客户认证结果', name: 'Identity_result', required: 'Y', defaults: '默认0', remarks:'0-未进行身份认证 1-身份认证成功 2-身份认证失败'},
                {desc: '客户证件号', name: 'Id_no', required: 'N', defaults: '', remarks:'IdentityResult=1时必填'},
                {desc: '客户证件类型', name: 'Id_type', required: 'N', defaults: '', remarks:'身份证-I，   IdentityResult=1时必填'},
                {desc: '客户姓名', name: 'name', required: 'N', defaults: '', remarks:''},
                {desc: '客户性别', name: 'sex', required: 'N', defaults: '', remarks:'男：M 女：F'},
                {desc: '客户生日', name: 'birth_date', required: 'N', defaults: '', remarks:'格式：yyyy-MM-dd'},
                {desc: '操作员工号', name: 'clerk_no', required: 'N', defaults: '', remarks:''},
                {desc: '操作员证件类型', name: 'clerkId_type', required: 'N', defaults: '', remarks:''},
                {desc: '操作员证件号', name: 'clerk_id_no', required: 'N', defaults: '', remarks:''},
                {desc: '操作员姓名', name: 'clerk_name', required: 'N', defaults: '', remarks:''},
                {desc: '操作员类型', name: 'clerk_type', required: 'N', defaults: '', remarks:'0：柜面业务员 1:销售员'},
                {desc: '操作员所属机构号', name: 'branch_no', required: 'N', defaults: '', remarks:''}
            ]
        }
    },
    methods: {
        custormAnchor(anchorName) {
            // 找到锚点
            let anchorElement = document.getElementById(anchorName);
            // 如果对应id的锚点存在，就跳转到锚点
            if(anchorElement) { anchorElement.scrollIntoView(); }
        }
    }
}
</script>
<style lang="stylus">
    .doc-wrapper
        h5
            font-weight: 600
</style>


