        var crypto = require('crypto');
        functionaesEncrypt(data, secretKey){
        var cipher = crypto.createCipher('aes-128-ecb', secretKey);
        return cipher.update(data,'utf8','hex')+ cipher.final('hex');}
        functionaesDecrypt(data, secretKey){
