        <h5 class="mt20">3-2、Java</h5>
        <pre>
        import javax.crypto.Cipher;import javax.crypto.spec.SecretKeySpec;import java.security.MessageDigest;

        /**
        * AES加解密库，可与NodeJS、C#等语言互相加解密
        */
        publicclassAesUtil{
            publicstaticfinal String DEFAULT_CODING ="utf-8";
        }

        /**
        * AES加密（AES/ECB/PKCS5Padding）
        */
        publicstatic String encrypt(String content, String key)throws Exception {
            byte[] input = content.getBytes(DEFAULT_CODING);

            MessageDigest md = MessageDigest.getInstance("MD5");
            byte[] digest = md.digest(key.getBytes(DEFAULT_CODING));
            SecretKeySpec sks =newSecretKeySpec(digest,"AES");
            Cipher cipher = Cipher.getInstance("AES/ECB/PKCS5Padding");
            cipher.init(Cipher.ENCRYPT_MODE, sks);

            byte[] cipherText =newbyte[cipher.getOutputSize(input.length)];
            int ctLength = cipher.update(input,0, input.length, cipherText,0);
            ctLength += cipher.doFinal(cipherText, ctLength);

            returnparseByte2HexStr(cipherText);
        }

        /**
        * AES解密（AES/ECB/PKCS5Padding）
        */
        privatestatic String decrypt(String encrypted, String key)throws Exception {
            MessageDigest md = MessageDigest.getInstance("MD5");
            byte[] digest = md.digest(key.getBytes(DEFAULT_CODING));
            SecretKeySpec sks =newSecretKeySpec(digest,"AES");
            Cipher dcipher = Cipher.getInstance("AES");
            dcipher.init(Cipher.DECRYPT_MODE, sks);

            byte[] contentBytes = dcipher.doFinal(toByte(encrypted));
            returnnewString(contentBytes);
        }

        /**
        * 字符串转字节数组
        */
        privatestaticbyte[]toByte(String hexString){
            int len = hexString.length()/2;
            byte[] result =newbyte[len];
            for(int i =0; i < len; i++){
                result[i]= Integer.valueOf(hexString.substring(2* i,2* i +2),16).byteValue();
            }
            return result;
        }

        /**
        * 字节转16进制数组
        */
        privatestatic String parseByte2HexStr(byte buf[]){
            StringBuffer sb =newStringBuffer();
            for(int i =0; i < buf.length; i++){
                String hex = Integer.toHexString(buf[i]&0xFF);
                if(hex.length()==1){
                    hex ='0'+ hex;
                }
                sb.append(hex);
            }
            return sb.toString();
        }

        </pre>
        <h5 class="mt20">3-3、C#</h5>
        <pre>
        using System;using System.Collections.Generic;using System.Linq;using System.Security.Cryptography;
        using System.Text;using System.Threading.Tasks;
        namespace AesUtilDemo
        {
            classAesUtil
            {
                staticvoidMain(string[] args)
                {
                string content = "hello";
                string key = "12345";
                string encrypted = AesEncrypt(content, key);
                Console.WriteLine(encrypted);
                Console.WriteLine(AesDescrypt(encrypted, key));
                Console.ReadLine();
                }

                publicstaticstringAesEncrypt(string content,string key)
                {
                    Byte[] contentBytes = Encoding .UTF8.GetBytes(content);
                    System.Security.Cryptography. RijndaelManaged rm =newSystem.Security.Cryptography.RijndaelManaged
                    {
                        Key =getKeyMd5(key),
                        Mode = System.Security.Cryptography. CipherMode.ECB,
                        Padding = System.Security.Cryptography. PaddingMode.PKCS7
                    };

                    System.Security.Cryptography. ICryptoTransform cTransform = rm.CreateEncryptor();
                    Byte[] resultArray = cTransform.TransformFinalBlock(contentBytes,0, contentBytes.Length);

                    returnbytes2HexString(resultArray);
                }

                publicstaticstringAesDescrypt(string encrypted,string key)
                {
                    Byte[] encryptedBytes =hexString2Bytes(encrypted);

                    System.Security.Cryptography. RijndaelManaged rm =newSystem.Security.Cryptography.RijndaelManaged
                    {
                        Key =getKeyMd5(key),
                        Mode = System.Security.Cryptography. CipherMode.ECB,
                        Padding = System.Security.Cryptography. PaddingMode.PKCS7
                    };

                    System.Security.Cryptography. ICryptoTransform cTransform = rm.CreateDecryptor();
                    Byte[] resultArray = cTransform.TransformFinalBlock(encryptedBytes,0, encryptedBytes.Length);

                    return UTF8Encoding .UTF8.GetString(resultArray);
                }

                privatestaticbyte[]getKeyMd5(string key)
                {
                    byte[] result = Encoding .UTF8.GetBytes(key);
                    MD5 md5 =newMD5CryptoServiceProvider();
                    return md5.ComputeHash(result);
                }

                privatestaticstringbytes2HexString(byte[] bytes)
                {
                    StringBuilder sb =newStringBuilder();
                    foreach(byte b in bytes)
                    {
                        sb.AppendFormat("{0:x2}", b);
                    }
                    return sb.ToString();
                }

                privatestaticbyte[]hexString2Bytes(string hexString)
                {
                    byte[] result =newbyte[hexString.Length /2];
                    for(int i =0; i < result.Length; i++)
                    {
                        result[i]= Convert.ToByte(hexString.Substring(i *2,2),16);
                    }
                    return result;
                }
            }
        }
