import Vue from 'vue'
import Router from 'vue-router'
// 开始
import Summary from '@/components/start/Summary'
import Intro from '@/components/start/Intro'

// 云服务
import CloudDoc from '@/components/cloud/Doc'
import Appendix from '@/components/cloud/Appendix'
// 公共报文头
import MessageHead from '@/components/common/MessageHead'
// 客户权益视图
import EquityView from '@/components/equityview/EquityView'
// 保单借款
import LoanAccept from '@/components/loan/LoanAccept'
// 保单还款
import Repay from '@/components/repay/Repay'
// 生存金领取
import Survival from '@/components/survival/Survival'
// 满期金领取
import Expira from '@/components/expira/Expira'
// 客户联系方式变更
import Contact from '@/components/contact/Contact'
// 转账授权
import Grant from '@/components/grant/Grant'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Summary',
      component: Summary
    },
    {
      path: '/start/summary',
      name: 'Summary',
      component: Summary
    },
    {
      path: '/start/intro',
      name: 'Intro',
      component: Intro
    },
    {
      path: '/cloud/doc',  // 保全云服务
      name: 'CloudDoc',
      component: CloudDoc
    },
    {
      path: '/cloud/appendix',  // 保全云服务 -> 附录
      name: 'Appendix',
      component: Appendix
    },
    {
      path: '/msgHead',  // 公共报文头
      name: 'MessageHead',
      component: MessageHead
    },
    {
      path: '/contact/doc',  // 客户联系方式变更
      name: 'Contact',
      component: Contact
    },
    {
      path: '/loan/doc',  // 借款
      name: 'LoanAccept',
      component: LoanAccept
    },
    {
      path: '/repay/doc',  // 还款
      name: 'Repay',
      component: Repay
    },
    {
      path: '/survival/doc',  // 生存金领取
      name: 'Survival',
      component: Survival
    },
    {
      path: '/expira/doc',  // 待领满期金
      name: 'Expira',
      component: Expira
    },
    {
      path: '/grant/doc',  // 转账授权
      name: 'Grant',
      component: Grant
    },
    {
      path: '/equityview/doc',  // 客户权益视图
      name: 'EquityView',
      component: EquityView
    }
  ]
});



