<template>
  <div class="panel-group" id="accordion" role="tablist" ref="affixEl">
    <div class="panel panel-default">
      <!-- BEGIN 开始 -->
      <div class="panel-heading" role="tab">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#start">
            开始
          </a>
        </h4>
      </div>
      <div id="start" class="panel-collapse collapse in" role="tabpanel">
        <div class="panel-body">
          <router-link to="/start/summary">概述</router-link>
          <router-link to="/start/intro">接入说明</router-link>
        </div>
      </div>
      <!-- END 开始 -->

      <!-- BEGIN 页面接入 -->
      <div class="panel-heading" role="tab" style="border-top: solid 1px #eaeaea">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#pageAccess">
            页面级别接入
          </a>
        </h4>
      </div>
      <div id="pageAccess" class="panel-collapse collapse" role="tabpanel">
        <div class="panel-body">
          <router-link to="/cloud/doc">保全云服务</router-link>
          <router-link to="/cloud/appendix">附录</router-link>
        </div>
      </div>
      <!-- END 页面接入 -->

      <!-- BEGIN 服务接入 -->
      <div class="panel-heading" role="tab" style="border-top: solid 1px #eaeaea">
        <h4 class="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#accordion" href="#serviceAccess">
            服务级别接入
          </a>
        </h4>
      </div>
      <div id="serviceAccess" class="panel-collapse collapse" role="tabpanel">
        <div class="panel-body">
          <router-link to="/msgHead">公共报文头</router-link>
          <router-link to="/equityview/doc">客户权益视图</router-link>
          <router-link to="/loan/doc">保单借款</router-link>
          <router-link to="/repay/doc">保单还款</router-link>
          <router-link to="/survival/doc">生存金领取</router-link>
          <router-link to="/expira/doc">满期金领取</router-link>
          <router-link to="/contact/doc">客户联系方式变更</router-link>
          <router-link to="/grant/doc">授权转账账户变更</router-link>
        </div>
      </div>
      <!-- END 服务接入 -->

    </div>
  </div>
</template>
<script>
export default {
  name: 'Siderbar',
  mounted () {
      this.$nextTick(() => {
          var affixEl = this.$refs.affixEl;
          $(affixEl).affix({
            offset: {
              top: 0
            }
          })
      })
  }
}
</script>
<style lang="stylus">
.panel-group
  width: 160px
  .panel-heading
    .panel-title
      a
        display: block
  .panel-collapse
    .panel-body
      padding: 0
      a
        display: block
        padding: 8px 16px
        color: #555
        &:hover
          background-color: #f8f8f8
        &:not(:last-child)
          border-bottom: solid 1px #ddd
    .router-link-exact-active
      border-left: solid 2px #009b63
      color: #009b63 !important

</style>


