<template>
    <div class="repay">
        <h3 class="text-center">接口功能说明</h3>
        <h4 class="page-header">索引</h4>
        <ul>
          <li><a href="" @click.prevent="custormAnchor('flowchart')">流程说明</a></li>
          <li><a href="" @click.prevent="custormAnchor('repay01')">一、待还款保单查询</a></li>
          <li><a href="" @click.prevent="custormAnchor('repay02')">二、客户银行信息查询</a></li>
          <li><a href="" @click.prevent="custormAnchor('repay03')">三、受理规则检查</a></li>
          <li><a href="" @click.prevent="custormAnchor('repay04')">四、还款受理提交</a></li>
        </ul>
        <h4 class="page-header" id="flowchart">流程说明</h4>
        <img src="./repay.png" alt="">
        <h4 class="page-header" id="repay01">一、待还款保单查询</h4>
        <h5 class="mt20">1.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/availableReplayPolService/availableRtn</p>
        <h5 class="mt20">1.2 入参说明</h5>
        <v-inParamTable :tableData="repayQueryParams"></v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{"birthDate":"1970-10-23","idNo":"152601197010231685","idType":"I","name":"董玉酶","sex":"F"}</pre>
        <h5 class="mt20">1.3 出参说明</h5>
        <v-outParamTable :tableData="repayQueryResParams" :isList=true></v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>
        [{"accOpenDate":1503504000000, "cntrNo":"2015110244518015063360", "hldrCustNo":"2000110100000322875", "hldrIdNo":"110105196103271920", "hldrIdType":"I", "hldrName":"文家邻", "ipsnCustNo":"2013110244500058075", "ipsnIdNo":"110108198906226929", "ipsnIdType":"I", "ipsnName":"张闻", "loanAmnt":500.0, "loanIntAmnt":8.85, "loanInterest":5.5, "loanTotalAmnt":508.85, "mgrBranchNo":"110244", "polCode":"518", "polName":"国寿鑫如意年金保险（白金版）", "reLoanTimes":0, "rtnDate":1519401600000, "rtnType":"1", "salesBranchno":"110244", "salesChannel":"PH", "salesClerkno":"308", "sysNo":"1"}]</pre>

        <h4 class="page-header" id="repay02">二、客户银行信息查询</h4>
        <h5 class="mt20">2.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/bankQueryService/bankQuery</p>
        <h5 class="mt20">2.2 入参说明</h5>
        <v-inParamTable :tableData="bankQueryParams"></v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{"idNo": "110105196103271920", "idType": "I", "name": "文家邻"}</pre>
        <h5 class="mt20">2.3 出参说明</h5>
        <v-outParamTable :tableData="bankQueryResParams" :isList="true"></v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>[{"accCustName":"文家邻","bankAccNo":"6225800101839847","bankCode":"1111","bankName":"招商银行","bankaccIdNo":"110105196103271920","bankaccIdType":"I"}]</pre>

        <h4 class="page-header" id="repay03">三、受理规则检查</h4>
        <h5 class="mt20">3.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/checkAcceptRuleService/ruleCheck</p>
        <h5 class="mt20">3.2 入参说明</h5>
        <v-commonSubmit>
            <tr>
                <td colspan="5" class="bg-success text-center">开始 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr> 
            <tr v-for="item in repayBusinessBOList">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.required}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-success text-center">结束 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr>
        </v-commonSubmit>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>
        "businessBOList":[{"cntrNo": "2015110244518015063360", "accOpenDate": 1503504000000, "loanAmnt": 500, "loanIntAmnt": 8.93, "loanInterest": 5.5, "reLoanNum": 0, "rtnAmnt": 508.93, "rtnType": "LI"}],
        "cSMtnInfoDtlBOList":[{"amnt": 508.93, "calAmnt": 508.93, "cntrNo": "2015110244518015063360", "hldrIdNo": "110105196103271920", "hldrIdType": "I", "hldrName": "文家邻", "ipsnIdNo": "110108198906226929", "ipsnIdType": "I", "ipsnName": "张闻", "mgrBranchNo": "110244", "mtnApplyNo": "20170000000000012221", "polCode": "518", "salesBranchNo": "110244", "salesChannel": "PH", "salesNo": "308", "sysNoOriginal": "1"}],
        "cSMtnInfoLstBO":{"acceptBranchNo": "000000", "acceptClerkNo": "csbps", "mtnApplyNo": "20170000000000012221", "mtnItemCode": "90", "totalAmnt": 508.93, "totalCalAmnt": ""},
        "cSMtnRpInfoBO":{"accCustIdNo": "110105196103271920", "accCustIdType": "I", "accCustName": "文家邻", "bankAccInType": "1", "bankAccNo": "6225800101839847", "bankCode": "1111", "grantFlag": "", "mioType": "J"},
        "csMtnAccClerkInfoBO":{"acceptBranchNo": "000000", "acceptClerkNo": "csbps"},
        "csMtnAssigneeInfoBO":{},
        "csMtnIdentityBO":{"mtnApplyNo": "20170000000000012221", "identityType": "4", "identifyResult": "1"},
        "csMtnInfoBO":{"acceptChannel":"V", "applBirthDate":"1961-03-27", "applIdNo":"110105196103271920", "applIdType":"I", "applName":"文家邻", "applSex":"F", "custLevel":"", "custRiskLevel":", "idValidDate":"9999-12-31 00:00:00", "isAssignee": "0", "mtnApplyNo": "20170000000000012221"}</pre>
        <h5 class="mt20">3.3 出参说明</h5>
        <v-outParamTable :hasSlot="true">
            <tr>
                <td>受理结果信息</td>
                <td>msg</td>
                <td>String</td>
                <td></td>
            </tr>
        </v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>{"msg": "success"}</pre>

        <h4 class="page-header" id="repay04">四、还款受理提交</h4>
        <h5 class="mt20">3.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/mtnRepayCommitService/accept</p>
        <h5 class="mt20">3.2 入参说明</h5>
        <v-commonSubmit>
            <tr>
                <td colspan="5" class="bg-success text-center">开始 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr> 
            <tr v-for="item in repayBusinessBOList">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.required}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="bg-success text-center">结束 + 业务受理信息businessBOList&ltlist&gt</td>
            </tr>
        </v-commonSubmit>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>
        "businessBOList":[{"cntrNo": "2015110244518015063360", "accOpenDate": 1503504000000, "loanAmnt": 500, "loanIntAmnt": 8.93, "loanInterest": 5.5, "reLoanNum": 0, "rtnAmnt": 508.93, "rtnType": "LI"}],
        "cSMtnInfoDtlBOList":[{"amnt": 508.93, "calAmnt": 508.93, "cntrNo": "2015110244518015063360", "hldrIdNo": "110105196103271920", "hldrIdType": "I", "hldrName": "文家邻", "ipsnIdNo": "110108198906226929", "ipsnIdType": "I", "ipsnName": "张闻", "mgrBranchNo": "110244", "mtnApplyNo": "20170000000000012221", "polCode": "518", "salesBranchNo": "110244", "salesChannel": "PH", "salesNo": "308", "sysNoOriginal": "1"}],
        "cSMtnInfoLstBO":{"acceptBranchNo": "000000", "acceptClerkNo": "csbps", "mtnApplyNo": "20170000000000012221", "mtnItemCode": "90", "totalAmnt": 508.93, "totalCalAmnt": ""},
        "cSMtnRpInfoBO":{"accCustIdNo": "110105196103271920", "accCustIdType": "I", "accCustName": "文家邻", "bankAccInType": "1", "bankAccNo": "6225800101839847", "bankCode": "1111", "grantFlag": "", "mioType": "J"},
        "csMtnAccClerkInfoBO":{"acceptBranchNo": "000000", "acceptClerkNo": "csbps"},
        "csMtnAssigneeInfoBO":{},
        "csMtnIdentityBO":{"mtnApplyNo": "20170000000000012221", "identityType": "4", "identifyResult": "1"},
        "csMtnInfoBO":{"acceptChannel":"V", "applBirthDate":"1961-03-27", "applIdNo":"110105196103271920", "applIdType":"I", "applName":"文家邻", "applSex":"F", "custLevel":"", "custRiskLevel":", "idValidDate":"9999-12-31 00:00:00", "isAssignee": "0", "mtnApplyNo": "20170000000000012221"}</pre>
        
        <h5 class="mt20">3.3 出参说明</h5>
        <v-outParamTable :hasSlot="true">
            <tr>
                <td>保全业务顺序号</td>
                <td>mtnSeqNo</td>
                <td>String</td>
                <td>用于电子凭证查询</td>
            </tr>
        </v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>{"mtnSeqNo": "200010001"}</pre>
    </div>
</template>
<script>
import InParamTable from '../common/InParamTable';
import OutParamTable from '../common/OutParamTable';
import CommonSubmit from '../common/CommonSubmit';
import CommonOutParams from '../common/CommonOutParams';
export default {
    components: {
        'v-inParamTable': InParamTable,
        'v-outParamTable': OutParamTable,
        'v-commonSubmit': CommonSubmit,
        'v-commonOutParams': CommonOutParams
    },
    data() {
        return {
            repayQueryParams: [
                {chnName: '客户姓名 ', name: 'name', required: 'N', type: 'String', remarks: ''},
                {chnName: '证件类型 ', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件号码 ', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '性别 ', name: 'sex', required: 'N', type: 'String', remarks: ''},
                {chnName: '出生日期 ', name: 'birthDate', required: 'N', type: 'String', remarks: ''}
            ],
            repayQueryResParams: [
                {chnName: '保单号 ', name: 'cntrNo', type: 'String', remarks: ''},
                {chnName: '所属系统 ', name: 'sysNo', type: 'String', remarks: ''},
                {chnName: '管理机构号 ', name: 'mgrBranchNo', type: 'String', remarks: ''},
                {chnName: '险种名称 ', name: 'polName', type: 'String', remarks: ''},
                {chnName: '险种代码 ', name: 'polCode', type: 'String', remarks: ''},
                {chnName: '投保人客户号 ', name: 'hldrCustNo', type: 'String', remarks: ''},
                {chnName: '投保人姓名 ', name: 'hldrName', type: 'String', remarks: ''},
                {chnName: '投保人证件号 ', name: 'hldrIdNo', type: 'String', remarks: ''},
                {chnName: '投保人证件类型 ', name: 'hldrIdType', type: 'String', remarks: ''},
                {chnName: '被保人客户号 ', name: 'ipsnCustNo', type: 'String', remarks: ''},
                {chnName: '被保人姓名 ', name: 'ipsnName', type: 'String', remarks: ''},
                {chnName: '被保人证件类型 ', name: 'ipsnIdType', type: 'String', remarks: ''},
                {chnName: '被保人证件号 ', name: 'ipsnIdNo', type: 'String', remarks: ''},
                {chnName: '销售员机构 ', name: 'salesBranchno', type: 'String', remarks: ''},
                {chnName: '销售渠道 ', name: 'salesChannel ', type: 'String', remarks: ''},
                {chnName: '销售员工号 ', name: 'salesClerkno', type: 'String', remarks: ''},
                {chnName: '开户日期 ', name: 'accOpenDate', type: 'Date', remarks: ''},
                {chnName: '还款日期 ', name: 'rtnDate', type: 'Date', remarks: ''},
                {chnName: '现金价值 ', name: 'cashValue', type: 'Double', remarks: ''},
                {chnName: '借款金额 ', name: 'loanAmnt', type: 'Double', remarks: ''},
                {chnName: '借款利率 ', name: 'loanInterest', type: 'Double', remarks: ''},
                {chnName: '借款利息 ', name: 'loanIntAmnt', type: 'Double', remarks: ''},
                {chnName: '借款本息和 ', name: 'loanTotalAmnt', type: 'Double', remarks: ''},
                {chnName: '还息续借次数 ', name: 'reLoanTimes', type: 'long', remarks: ''},
                {chnName: '还款类型 ', name: 'rtnType', type: 'String', remarks: ''}
            ],
            bankQueryParams: [
                {chnName: '姓名', name: 'name', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件类型', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '证件号码', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '性别', name: 'sex', required: 'N', type: 'String', remarks: ''},
                {chnName: '出生日期', name: 'birthDate', required: 'N', type: 'String', remarks: ''}
            ],
            bankQueryResParams: [
                {chnName: '帐户所有人名称', name: 'accCustName', type: 'String', remarks: ''},
                {chnName: '银行代码', name: 'bankCode', type: 'String', remarks: ''},
                {chnName: '银行名称', name: 'bankName', type: 'String', remarks: ''},
                {chnName: '银行帐号', name: 'bankAccNo', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证件类型', name: 'bankaccIdType', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证件类型中文', name: 'bankaccIdTypeChn', type: 'String', remarks: ''},
                {chnName: '帐户所有人身份证号', name: 'bankaccIdNo', type: 'String', remarks: ''},
                {chnName: '预留手机号', name: 'mobile', type: 'String', remarks: ''}
            ],
            repayBusinessBOList: [
                {chnName: '业务凭证号', name: 'mtnNo', required: 'N', type: 'String', remarks: ''},
                {chnName: '保单号', name: 'cntrNo', required: 'Y', type: 'String', remarks: ''},
                {chnName: '开户日期', name: 'accOpenDate', required: 'Y', type: 'Date', remarks: ''},
                {chnName: '还款日期', name: 'rtnDate', required: 'N', type: 'Date', remarks: ''},
                {chnName: '借款金额', name: 'loanAmnt', required: 'Y', type: 'Double', remarks: ''},
                {chnName: '借款利息', name: 'loanIntAmnt', required: 'Y', type: 'Double', remarks: ''},
                {chnName: '借款利率', name: 'loanInterest', required: 'Y', type: 'Double', remarks: ''},
                {chnName: '还息续借次数', name: 'reloanNum', required: 'Y', type: 'Long', remarks: ''},
                {chnName: '还款类型', name: 'rtnType', required: 'Y', type: 'String', remarks: ''},
                {chnName: '还款金额', name: 'rtnAmnt', required: 'Y', type: 'Double', remarks: ''}
            ]
        }
    },
    mounted() {
        $('body,html').animate({scrollTop: 0}, 0);
    },
    methods: {
        custormAnchor(anchorName) {
            // 找到锚点
            let anchorElement = document.getElementById(anchorName);
            // 如果对应id的锚点存在，就跳转到锚点
            if(anchorElement) { anchorElement.scrollIntoView(); }
        }
    }
}
</script>
<style>
    
</style>


